import styled from 'styled-components'

type TilesProps = {
  children: React.ReactNode
}

const TilesContainer = styled.div`
  display: grid;
  gap: 16px;
`

export const Tiles = ({ children }: TilesProps) => {
  return <TilesContainer>{children}</TilesContainer>
}

type TilesItemProps = {
  title: string
  description: string
  icon?: JSX.Element
  onClick: () => void
}

const TilesItemContainer = styled.button`
  background-color: #f5f4f3;
  padding: 20px 24px;
  text-align: left;

  display: grid;
  grid-template-columns: auto 1fr;
  gap: 20px;

  svg {
    display: block;
    width: 24px;
    height: 24px;
  }
`

const TilesItemTitle = styled.div`
  color: #10110d;
  font-family: "Suisse Int'l";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 140%; /* 28px */
`

const TilesItemDescription = styled.div`
  margin-top: 8px;

  color: #6f6f69;
  font-family: "Suisse Int'l";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 24px */
`

Tiles.Item = ({ title, description, icon, onClick }: TilesItemProps) => (
  <TilesItemContainer onClick={onClick}>
    {icon && <div>{icon}</div>}
    <div>
      <TilesItemTitle>{title}</TilesItemTitle>
      <TilesItemDescription>{description}</TilesItemDescription>
    </div>
  </TilesItemContainer>
)
